<template>
    <div class="login">
        <div class="page">
            <div class="picture"></div>

            <div class="information">
                <el-form
                    :model="form"
                    status-icon
                    ref="from"
                    label-width="80px"
                    class="login-form">
                    <h1 class="title-zc">登录</h1>
                    <el-form-item label="账号" prop="username">
                        <el-input v-model.number="form.username"></el-input>
                    </el-form-item>

                    <el-form-item label="密码" prop="password">
                        <el-input
                            type="password"
                            v-model="form.password"
                            autocomplete="off">
                        </el-input>
                    </el-form-item>
                    
                    <div class="remember">
                        <input class="small" type=" checkbox"/>
                        <div class="choose">记住用户名</div>
                    </div>
                    <el-form-item>
                        <el-button type="primary" style="width:300px;" @click="submitForm">登录</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            form:{
                username:"",
                password:"",
            },
        }
    },
    methods:{
        submitForm(){
        //    console.log('===',this.form)
            if(this.form.username && this.form.password) {
                localStorage.setItem('isAuthenticated', true);//localStorage本地储存isAuthenticated是否通过身份验证
                
                this.$router.push('/')//登入成功
            }
            else {
                localStorage.setItem('isAuthenticated', false);//登入失败重新登入
            }
        }
    }
}
</script>

