import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home'
import Form from './views/form.vue';
import Login from  './views/login.vue'
import Menu from  './views/menu.vue'
import Page from  './views/page.vue'


Vue.use(Router)

const router = new Router({
    routes: [
        {
            path: '/login',
            name: 'login',
            component: Login,
        },
        {
            path: '/',
            name: 'menu',
            component: Menu,
            children: [
                {
                    path: '/page',
                    name: 'page',
                    component: Page,
                },
                {
                    path: '/home',
                    name: 'home',
                    component: Home
                },
                {
                    path: '/form',
                    name: 'form',
                    component: Form
                },
            ],
        }
    ],  
})
router.beforeEach((to, from, next) => {
    const isAuthenticated = localStorage.getItem('isAuthenticated');//localStorage本地储存
    console.log('navigation-guards', to, from, next);//导航守卫
    if (to.name !== 'login' && !isAuthenticated) {//isAuthenticated是否通过身份验证
        next({ name: 'login' })//登入失败重新登入
        return false;
    }
    
    if (to.name === 'login' && isAuthenticated) {
        next({ name: 'menu' });//登入正确
        return false;
    }

    next()
})
export default router;
